import React, { Component } from "react";

class PopFloorFooter extends Component {
  constructor() {
    super();
  }
  render() {
    return (
      <footer className="footer">
        <p>未领取的红包，将于24小时后发起退款</p>
      </footer>
    );
  }
}
export default PopFloorFooter;



