import React, { Component } from "react";
import Header from "../../../../../../base-page/Header";
import "./index.less";

class Photo extends Component {
  constructor() {
    super();
  }
  handleClick = () => {
    window.history.go(0);
  };
  render() {
    return (
      <div>
        <Header>
          <li onClick={this.handleClick}>取消</li>
          <li>设置名字</li>
          <li>完成</li>
        </Header>
        <div className="name-content">
          <input value="会跑的鸡腿" />
        </div>
      </div>
    );
  }
}
export default Photo;



